import { Tooltip } from "@mui/material";
import styles from "./Tiles.scss";

const Tiles = ({ data, type, customClass }) => {
  let tiles =
    data &&
    data.length > 0 &&
    data.map((tilesData, i) => {
      return (
        <Tooltip
          title={tilesData.cursorTitle ? tilesData.cursorTitle : ""}
          followCursor
          placement="top"
          key={i + tilesData.text}
        >
          <div className={styles.tiles} onClick={tilesData.onTilesClick}>
            {tilesData.title && (
              <div className={styles.title}>{tilesData.title}</div>
            )}
            <img src={tilesData.image} alt="tiles image" />
            <div className={styles.text}>{tilesData.text}</div>
          </div>
        </Tooltip>
      );
    });
  return (
    <div
      className={`${styles.tiles_wrap} ${customClass ? customClass : ""} ${
        type == "weatherTiles" ? styles.weather_tiles : ""
      }`}
    >
      {tiles}
    </div>
  );
};

export default Tiles;
